import React, { useContext } from 'react';
import '../../node_modules/video-react/dist/video-react.css';
import styled from 'styled-components';
import Context from '../Context';
import { getClip } from '../utils';
import { displayFlex, dropShadow } from '../constants';

const Wrapper = styled.div`
    ${displayFlex}
    ${dropShadow}
    margin: auto;
    width: 40rem;
    height: 25rem;
    padding: 1rem;
    background: black;
`;

const Video = styled.video`
  height: inherit;
`;

export default function VideoStream(props) {
  const { vidIds } = props;

  const {
    sequenceIndex, setSequenceIndex, videoIndex, setVideoIndex, videoSequences,
  } = useContext(Context);

  const onEnded = () => {
    setVideoIndex(videoIndex + 1);
    if (videoIndex + 1 >= vidIds.length) {
      const newSequenceIndex = (sequenceIndex + 1) % videoSequences.length;
      setSequenceIndex(newSequenceIndex);
      setVideoIndex(0);
    }
  };

  return (
    <Wrapper>
      <Video
        muted
        autoPlay
        onEnded={onEnded}
        src={getClip(vidIds[videoIndex]).clip}
      />
    </Wrapper>

  );
}
