import React from 'react';
import RaisedButton from './RaisedButton';

const styles = {
  fontSize: '2rem',
  padding: '1rem',
  margin: '1rem',
};

export default function EnterButtonComponent(props) {
  const { enterVideoSequence, disabled } = props;

  return (
    <RaisedButton
      className="enter-button"
      onClick={() => enterVideoSequence()}
      hover="yellow"
      background="red"
      color="yellow"
      style={styles}
      disabled={disabled()}
    >
      Enter
    </RaisedButton>

  );
}
