import React, { useContext } from 'react';
import styled from 'styled-components';
import VideoInput from './VideoInput';
import EnterButton from './Buttons/EnterButton';
import Context from '../Context';
import { width100 } from '../constants';

import VidNetClient from '../API/VidNetClient';

const client = new VidNetClient();

const EndComponent = styled.div`
  ${width100}
  max-width: 45rem;
  margin-top: 4rem;
  display: flex;
`;

const Initials = styled.div`
  ${width100}
  display: flex;
  justify-content: center;
  align-items: center;
`;

const validInitials = (initials, videoSequences) => {
  const lengthCorrect = initials.length === 3;
  const unique = !videoSequences.some(object => object.Initials === initials);
  return lengthCorrect && unique;
};


export default function End() {
  const {
    narrative, setClips, setNarrative, videoSequences,
  } = useContext(Context);

  const [initials, setInitials] = React.useState('   ');

  const enterVideoSequence = () => {
    const newSequence = { Initials: initials, Sequence: narrative };
    videoSequences.push(newSequence);
    setClips(narrative);
    setNarrative([]);
    client.postVideoSequence(newSequence);
  };

  return (
    <EndComponent>
      <Initials>
        <VideoInput index={0} initials={initials} setInitials={setInitials} />
        <VideoInput index={1} initials={initials} setInitials={setInitials} />
        <VideoInput index={2} initials={initials} setInitials={setInitials} />
      </Initials>
      <EnterButton
        disabled={() => !validInitials(initials, videoSequences)}
        enterVideoSequence={enterVideoSequence}
      />
    </EndComponent>

  );
}
